#scrollable-div {
    overflow-y: auto;
    position: absolute;
    top: 0;
    left: calc(100vw - 100%);
    right: 0;
    bottom: calc(100vh - 100%);
    scroll-behavior: smooth;
}

/* Hide scrollbar for Chrome, Safari and Opera */
#scrollable-div::-webkit-scrollbar {
    display: none;
    z-index: 10000px;
}

/* Hide scrollbar for IE, Edge and Firefox */
#scrollable-div {
    -ms-overflow-style: none;
    scrollbar-width: none;
} 

.timeline-container {
    min-height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 175px 0;
}

.timeline {
    width: 80%;
    height: auto;
    max-width: 800px;
    margin: 0 auto;
    position: relative;
}

.timeline-list {
    padding-left: 0px;
    text-align: left;
}

.timeline .timeline-list {
    list-style: none;
}

.timeline .timeline-list .timeline-item {
    padding: 20px;
    background-color: rgba(30, 31, 34, 70%);
    color: white;
    border-radius: 10px;
    margin-bottom: 20px !important;
}

.timeline .timeline-list .timeline-item:last-child {
    margin-bottom: 0px;
}

.timeline-event {
    font: normal normal normal 25px/30px Poppins SemiBold;
    text-shadow: 0 0 15px rgb(5, 0, 0);
    margin-bottom: 10px;
}

.timeline-detail {
    font: normal normal normal 17px/30px Poppins Regular;
    text-shadow: 0 0 15px rgb(5, 0, 0);
}

.timeline-content .timeline-date {
    font: normal normal normal 13px/30px Poppins Light;
    text-shadow: 0 0 15px rgb(5, 0, 0);
    margin-bottom: 10px;
    letter-spacing: 2px;
}

@media only screen and (min-width: 768px) {
    .timeline::before {
        content: '';
        position: absolute;
        height: 100%;
        width: 2px;
        left: 50%;
        transform: translateX(-50%);
        background-color: gray;
    }

    .timeline .timeline-list .timeline-item {
        width: 50%;
        margin-bottom: 50px !important;
        position: relative;
    }

    .timeline .timeline-list .timeline-item:nth-child(odd) {
        float: left;
        clear: right;
        transform: translateX(-30px);
        border-radius: 20px 0px 20px 20px;
    }

    .timeline .timeline-list .timeline-item:nth-child(even) {
        float: right;
        clear: left;
        transform: translateX(30px);
        border-radius: 0px 20px 20px 20px;
    }

    .timeline .timeline-list .timeline-item::after {
        content: '';
        position: absolute;
        height: 20px;
        width: 20px;
        background-color: gray;
        border-radius: 50%;
        top: 0;
    }

    .timeline .timeline-list .timeline-item:nth-child(odd)::after {
        transform: translate(50%, -50%);
        right: -30px;
    }

    .timeline .timeline-list .timeline-item:nth-child(even)::after {
        transform: translate(-50%, -50%);
        left: -30px;
    }

    .timeline .timeline-list .timeline-item:hover:after {
        transition: all 0.2s ease-in;
        background-color: white;
        box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
    }

    .timeline-content .timeline-date {
        position: absolute;
        top: -28px;
    }
}