.banner-image {
  background-image: url("../../assets/Home/home.jpg");
  background-size: fill;
  background-attachment: fixed;
  min-height: 100%;
}

@keyframes animate {
  from {
      text-shadow: 0 0 20px rgb(5, 0, 0);
  }
  to {
      text-shadow: 0 0 30px rgb(5, 0, 0), 0 0 10px rgb(5, 0, 0);
  }
}

@media only screen and (min-width: 1311px) {
  #home-text {
    line-height: 5.4rem;
    letter-spacing: -0.03em;
    font: normal normal normal 180px/1.4em classic;
    animation: animate 1s ease-in-out infinite alternate;
    color: rgba(255, 255, 255, 0.95);
  }
}

@media only screen and (min-width: 768px) and (max-width: 1310px) {
  #home-text {
    line-height: 5.4rem;
    letter-spacing: -0.03em;
    font: normal normal normal 120px/1.4em classic;
    animation: animate 1s ease-in-out infinite alternate;
    color: rgba(255, 255, 255, 0.95);
  }
}

@media only screen and (min-width: 580px) and (max-width: 767px) {
  #home-text {
    line-height: 5.4rem;
    letter-spacing: -0.03em;
    font: normal normal normal 70px/1.4em classic;
    animation: animate 1s ease-in-out infinite alternate;
    color: rgba(255, 255, 255, 0.95);
  }
}

@media only screen and (max-width: 579px) {
  #home-text {
    line-height: 5.4rem;
    letter-spacing: -0.03em;
    font: normal normal normal 45px/1.4em classic;
    animation: animate 1s ease-in-out infinite alternate;
    color: rgba(255, 255, 255, 0.95);
  }
}