.container-md {
    position: fixed;
}

.about-text {
    color: white;
    /* box-shadow: 0 0 10px rgba(255, 255, 255, 0.75); */
}

.paragraph {
    width: 97.8%;
    
}

.button {
    color: white;
    border-color: white;
    font: normal normal normal 15px Poppins ExtraLight;
}

.about-section {
    min-height: 100vh;
    /* width: 100%; */
    /* display: flex; */
    align-items: center;
    justify-content: center;
    /* padding: 175px 0; */
}

.timer {
    display: inline !important;
}

.timer::before {
    content: " ";
}

.timer-span::before {
    content: " ";
}

.resume-button {
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
    font: normal normal normal 15px/22px Poppins Light;
}

.resume-button:hover {
    color: white;
    transition: all 0.2s ease-in;
    border-color: rgba(255, 255, 255, 0.7);
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.35);
    background-color: rgba(255, 255, 255, 0.1);
}

.resume-button:focus, .resume-button:active {
    outline: none !important;
    box-shadow: none;
}

.about-image {
    box-shadow: 0 0 70px rgba(0, 0, 0, 0.9);
}

.about-main {
    text-shadow: 0 0 40px rgba(0, 0, 0, 0.9);
}

.about-languages {
    text-shadow: 0 0 40px rgba(0, 0, 0, 0.9);
}

.about-education {
    text-shadow: 0 0 40px rgba(0, 0, 0, 0.9);
}

.about-time-coding {
    text-shadow: 0 0 40px rgba(0, 0, 0, 0.9);
}

@media only screen and (min-width: 1400px) {
    .about-image {
        background-image: url("../../assets/About/about.jpg");
        height: 485px;
        width: 485px;
        min-height: 485px;
        min-width: 485px;
        background-size: cover;
        border-radius: 50%;
    }

    .about-main {
        font: normal normal normal 32px/41px Poppins SemiBold;
        margin-bottom: 20px;
        width: 100%;
    }
    
    .about-languages {
        font: normal normal normal 21px/30px Poppins Regular;
        margin-bottom: 20px;
    }
    
    .about-education {
        font: normal normal normal 17.4px/20px Poppins Light;
        margin-bottom: 10px;
    }
    
    .about-time-coding {
        font: normal normal normal 17.4px/20px Poppins Light;
        margin-bottom: 30px;
    }
}

