@media only screen and (max-width: 767px) {

    .container-md {
        padding-top: 0px;
    }

    .about-section {
        display: table;
    }

    .about-image {
        margin: auto;
        margin-top: 28%;
        margin-bottom: 30px;
    }

    .paragraph {
        text-align: center;
    }

    .resume-button {
        display: table;
        margin: 0 auto !important;
        font: normal normal normal 12px/13px Poppins ExtraLight;
    }

    .about-image {
        background-image: url("../../assets/About/about.jpg");
        height: 240px;
        width: 240px;
        background-size: cover;
        border-radius: 50%;
        min-width: 240px;
        min-height: 240px;
        padding-right: 20px !important;
    }

    .about-main {
        font: normal normal normal 16px/23px Poppins SemiBold;
        margin-bottom: 15px;
    }
    
    .about-languages {
        font: normal normal normal 13px/17px Poppins Regular;
        margin-bottom: 10px;
    }

    .about-education {
        font: normal normal normal 12.5px/15px Poppins Light;
        margin-bottom: 10px;
    }
    
    .about-time-coding {
        font: normal normal normal 10.9px/12px Poppins Light;
        margin-bottom: 15px;
    }
}