.contact-text {
    color: white;
}

.contacts-container {
    min-height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 175px 0;
}

.contacts {
    height: auto;
    max-width: 800px;
    margin: 0 auto;
    position: relative;
    margin-bottom: 0px;
}

.contact-links {
    font: normal normal normal 45px/45px Poppins SemiBold;
    text-shadow: 0 0 20px rgb(5, 0, 0);
    padding: 50px;
}

.contact-link {
    color: white;
    text-decoration: none;
}

.contact-link:hover {
    color: white;
    text-decoration: underline;
}

.contact-info {
    font: normal normal normal 27px/45px Poppins ExtraLight;
    text-shadow: 0 0 20px rgb(5, 0, 0);
}